.detail-wrap {
  background: var(--color-list-bg);
  padding: 56px 0 80px;
}

.detail-inner {
  max-width: 1160px;
  margin: 0 auto;
  background: var(--color-bg);
  border-radius: 24px;
  border: 1px solid var(--color-border);
  box-shadow: 0 18px 40px var(--color-shadow-mid);
  padding: 32px 40px 28px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.detail-inner::before {
  content: "";
  position: absolute;
  left: 40px;
  right: 40px;
  top: 0;
  height: 3px;
  border-radius: 999px;
  background: var(--color-header-bg);
}

.detail-top {
  display: flex;
  gap: 40px;
  align-items: flex-start;
  margin-top: 20px;
  margin-bottom: 28px;
}

.detail-img {
  flex: 0 0 460px;
  aspect-ratio: 4 / 3;
  border-radius: 20px;
  overflow: hidden;
  background: var(--color-card-bg);
}

.detail-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.detail-info {
  flex: 1;
}

.detail-label {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 500;
  color: var(--color-green-dark);
}

.detail-title {
  margin-bottom: 14px;
  font-size: 26px;
  font-weight: 700;
  color: var(--color-list-title);
}

.detail-main-text {
  font-size: 14px;
  color: var(--color-list-sub);
  line-height: 1.7;
}

.detail-btns {
  margin-top: 24px;
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.detail-btn {
  padding: 9px 22px;
  border-radius: 999px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid var(--color-list-btn-outline-border);
  background: var(--color-list-btn-outline-bg);
  color: var(--color-list-title);
}

.detail-btn.outline:hover {
  background: var(--color-btn-hover);
}

.detail-btn.primary {
  background: var(--color-list-btn-primary-bg);
  border-color: var(--color-list-btn-primary-bg);
  color: var(--color-list-btn-primary-text);
}

.detail-btn.primary:hover {
  background: var(--color-list-btn-primary-hover-bg);
}

/* 탭 영역 */
.detail-tabs {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid var(--color-border);
  margin-top: 12px;
  gap: 2px;
}

.tab {
  flex: 1;
  padding: 14px 0;
  font-size: 14px;
  font-weight: 500;
  background: transparent;
  border: none;
  cursor: pointer;
  color: var(--color-list-sub);
  transition: 0.2s;
  border-radius: 12px 12px 0 0;
}

.tab:hover {
  background: var(--color-list-card-bg);
}

.tab.active {
  color: var(--color-navy-deep);
  font-weight: 700;
  background: var(--color-bg);
  box-shadow: 0 -3px 0 var(--color-primary) inset;
}

.detail-tab-content {
  background: var(--color-list-card-bg);
  border-radius: 0 0 16px 16px;
  padding: 20px;
  margin-top: 0;
}

.detail-panel {
  font-size: 14px;
  color: var(--color-list-title);
  line-height: 1.7;
}

.detail-panel ul {
  padding-left: 18px;
}

.detail-panel li {
  margin-bottom: 4px;
}

.info-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.info-row {
  border-bottom: 1px dashed var(--color-border);
  padding-bottom: 6px;
}

.info-row strong {
  font-size: 13px;
  color: var(--color-list-sub);
}

.info-row span {
  font-size: 13px;
  color: var(--color-list-title);
}
