.support-page {
  padding-top: 40px;
  padding-bottom: 80px;
}

.support-inner {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box;
  font-family: "Pretendard", "Noto Sans KR", system-ui, -apple-system, sans-serif;
}

/* 헤더 */
.support-header {
  margin-bottom: 20px;
}

.support-title {
  margin: 0 0 6px;
  font-size: 30px;
  font-weight: 700;
  color: var(--color-list-title);
}

/* 탭 메뉴 */
.support-tabs {
  display: inline-flex;
  gap: 0;
  margin: 20px 0 18px;
  border-radius: 999px;
  padding: 3px;
  border: 1px solid var(--color-border);
  background: #ffffff;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.08);
}

.support-tab-btn {
  border: none;
  background: transparent;
  padding: 8px 18px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 999px;
  color: var(--color-list-sub);
  font-weight: 500;
  transition: background 0.15s, color 0.15s, box-shadow 0.15s, transform 0.1s;
}

.support-tab-btn.active {
  background: var(--color-header-bg);
  color: var(--color-text);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.18);
  transform: translateY(-1px);
}

.support-tab-btn:not(.active):hover {
  background: rgba(0, 0, 0, 0.03);
}

/* 탭 내용 전체 래퍼 */
.support-tab-panel {
  margin-top: 8px;
}

/* 공통 섹션 타이틀 */
.support-section-title {
  margin: 0 0 14px;
  font-size: 20px;
  font-weight: 700;
  color: var(--color-list-title);
}

/* FAQ 섹션 */
.support-faq {
  margin-bottom: 24px;
}

.faq-list {
  border-radius: 16px;
  border: 1px solid var(--color-border);
  background: #ffffff;
  overflow: hidden;
  box-shadow: 0 10px 26px rgba(0, 0, 0, 0.1);
}

.faq-item + .faq-item {
  border-top: 1px solid var(--color-border);
}

.faq-question {
  width: 100%;
  padding: 12px 16px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  font-size: 14px;
  text-align: left;
  color: var(--color-list-title);
  box-sizing: border-box;
}

.faq-item-active .faq-question {
  background: rgba(0, 124, 96, 0.04);
}

.faq-arrow {
  font-size: 12px;
  color: var(--color-list-sub);
  transition: transform 0.2s ease;
}

.faq-arrow.open {
  transform: rotate(180deg);
}

.faq-answer {
  padding: 8px 16px 14px;
  font-size: 13px;
  color: var(--color-list-sub);
  background: #ffffff;
}

/* FAQ 하단 연락처 인라인 섹션 */
.support-info-inline {
  margin-top: 18px;
  padding-top: 14px;
  border-top: 1px solid var(--color-border);
}

.support-info-inline-title {
  margin: 0 0 8px;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-list-title);
}

/* 1:1 문의 섹션 */
.support-contact {
  margin-top: 8px;
  background: #ffffff;
  border-radius: 20px;
  border: 1px solid var(--color-border);
  padding: 22px 22px 24px;
  box-sizing: border-box;
  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.16);
}

.support-contact-desc {
  margin: 0 0 16px;
  font-size: 13px;
  color: var(--color-list-sub);
}

.support-form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.support-form-row {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 14px 18px;
}

.support-field {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.support-field label {
  font-size: 13px;
  font-weight: 500;
  color: var(--color-list-title);
}

.support-field input,
.support-field select,
.support-field textarea {
  border-radius: 10px;
  border: 1px solid var(--color-border);
  padding: 9px 11px;
  font-size: 14px;
  outline: none;
  box-sizing: border-box;
  background: #ffffff;
  transition: border-color 0.2s, box-shadow 0.2s, background-color 0.2s;
}

.support-field textarea {
  resize: vertical;
  min-height: 120px;
}

.support-field input:focus,
.support-field select:focus,
.support-field textarea:focus {
  border-color: var(--color-header-bg);
  box-shadow: 0 0 0 3px rgba(58, 142, 135, 0.16);
}

/* 제출 버튼 */
.support-submit-btn {
  align-self: flex-end;
  margin-top: 6px;
  border-radius: 999px;
  border: none;
  padding: 10px 26px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  background: var(--color-header-bg);
  color: var(--color-text);
  transition: background 0.15s, box-shadow 0.15s, transform 0.1s, opacity 0.1s;
}

.support-submit-btn:hover {
  opacity: 0.96;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.22);
  transform: translateY(-1px);
}

.support-submit-btn:active {
  transform: translateY(0);
  box-shadow: none;
}

/* 연락처 카드 공통 스타일 (FAQ 하단에서 재사용) */
.support-info-card {
  background: #ffffff;
  border-radius: 10px;
  border: 1px solid var(--color-border);
  padding: 12px 14px 12px;
  box-sizing: border-box;
  margin-top: 4px;
}

.support-info-text {
  margin: 0 0 8px;
  font-size: 13px;
  color: var(--color-list-sub);
}

.support-info-list {
  list-style: none;
  padding: 0;
  margin: 0 0 6px;
}

.support-info-list li {
  display: flex;
  gap: 8px;
  font-size: 13px;
  color: var(--color-list-title);
}

.support-info-list li + li {
  margin-top: 4px;
}

.support-info-label {
  min-width: 60px;
  font-weight: 600;
  color: var(--color-list-title);
}

.support-info-value {
  color: var(--color-list-sub);
}

.support-info-notice {
  margin: 2px 0 0;
  font-size: 12px;
  color: var(--color-list-sub);
}

/* 반응형 */
@media (max-width: 900px) {
  .support-tabs {
    display: flex;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .support-inner {
    padding: 0 14px;
  }

  .support-contact {
    padding: 18px 16px 20px;
    border-radius: 16px;
  }

  .support-form-row {
    grid-template-columns: 1fr;
  }

  .support-tabs {
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.12);
  }
}
