.event-section {
   width: 100%;
   margin: 60px auto 0;
   padding: 0 240px;
}

.event-header {
   display: flex;
   align-items: center;
   gap: 16px;
   margin-bottom: 28px;
}

.event-header h2 {
   font-size: 26px;
   font-weight: 700;
   margin: 0;
}

.event-header p {
   color: #666;
   opacity: 0.9;
   font-size: 14px;
   margin: 0;
}

.more-btn {
   margin-left: auto;
   border: none;
   background: var(--color-primary);
   color: #171d2f;
   padding: 8px 14px;
   border-radius: 6px;
   font-size: 13px;
   cursor: pointer;
}

/* 카드 리스트 */
.event-list {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   gap: 28px;
}

/* 카드 */
.event-card {
   border-radius: 18px;
   padding: 24px 22px 28px;
   position: relative;
   overflow: hidden;
   box-shadow: 0 18px 30px rgba(0, 0, 0, 0.06);
   background: #ffffff;
   border: 1px solid rgba(0, 0, 0, 0.04);
}

.event-tag {
   font-size: 12px;
   font-weight: 600;
   color: var(--color-primary);
   background: rgba(243, 194, 0, 0.18);
   padding: 4px 10px;
   border-radius: 999px;
}

.event-title {
   font-size: 20px;
   font-weight: 700;
   margin: 16px 0 4px;
}

.event-sub {
   font-size: 15px;
   color: #444;
   margin-bottom: 18px;
}

.event-image {
   width: 100%;
   height: 180px;
   object-fit: cover;
   object-position: center;
   border-radius: 12px;
   margin-bottom: 16px;
}

.event-items {
   list-style: none;
   padding: 0;
   margin: 0;
}

.event-items li {
   display: flex;
   justify-content: space-between;
   padding: 5px 0;
   font-size: 14px;
   color: #333;
}

.event-items strong {
   font-weight: 700;
   color: #222;
}

